import axios from 'axios';
import qs from 'querystring';
import cg from '../configure.js';
var bp = cg.bp;

export default {
	state:{
		info:[],
	},
	getters:{
		info:state => state.info,
	},
	mutations:{
		allInfo:(state,val) => {
			state.info = val;
		},
	},
	actions:{
		//退出
		loginOut(context,obj){
			return new Promise((resolve,reject)=>{
				axios.post(bp+'/logoutUser',qs.stringify(obj)).then((result)=>{
					resolve(result);  //去data里拿到code
				}).catch((error)=>{
					reject(error);
				});
			});
		},
		//登录
		loginIn(context,obj){
			return new Promise((resolve,reject)=>{
				axios.post(bp+'/loginUser',qs.stringify(obj)).then((data)=>{
					context.commit('allInfo',data);
					resolve(data);
				}).catch((error)=>{
					reject(error);
				});
			});
		},
		//注册用户
		registerUser(context,obj){
			return new Promise((resolve,reject)=>{
				axios.post(bp+'/registerUser',qs.stringify(obj)).then((result)=>{
					resolve(result);
				}).catch((error)=>{
					reject(error);
				});
			});
		},
		//唯一性验证
		uniqueStu(context,obj){
           return new Promise((resolve,reject)=>{
			   axios.post(bp+'/findUserByName',qs.stringify(obj)).then((data)=>{
				   context.commit('allInfo',data);
				   resolve(data);
				}).catch((error)=>{
					reject(error);
				});
		   });
		},
	}
}



