import axios from 'axios';
import qs from 'querystring';
import cg from './configure.js';
var bp = cg.bp;

export default {
	state:{
		prodections:{},
	},
	getters:{
		prodections:state => state.prodections,
	},
	mutations:{
		prodections:(state,val) => {
			state.prodections = val;
		},
	},
	actions:{
		information(context,type){
           return new Promise((resolve,reject)=>{
			   axios.get(bp+'/allWebSocket?topic='+type).then((data)=>{
				   context.commit('prodections',data);
				   resolve(data);
				}).catch((error)=>{
					reject(error);
				});
		   });
		},
	}
}



