<template>
<div class="login" :style="style">
   <!-- v2.0 -->
  <div class="login_content">
    <div class="login_title">
        杰普大数据监测平台
    </div>
    <div class="login_body">
      <div class="one">
        <div class="left">
          <p>欢迎注册</p>
          <p>请输入您的相关信息</p>
        </div>
        <div class="right">
          <i class="fa fa-key" style="font-size: 66px;"></i>
        </div>
      </div>
      <div class="two">
        <el-form :model="registerForm" :rules='rules'  ref='registerForm' status-icon>
          <el-form-item prop='loginName' :label-width="fw" label='用户名' >
            <el-input v-model="registerForm.loginName" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item prop='password' :label-width="fw" label='密码'>
            <el-input type='password' v-model="registerForm.password" placeholder="请输入密码" @keyup.enter.native="register('loginForm')"></el-input>
          </el-form-item>
          <el-form-item prop='realName' :label-width="fw" label='姓名' >
            <el-input v-model="registerForm.realName" placeholder="请输入真是姓名"></el-input>
          </el-form-item>
           <el-form-item prop='demo' :label-width="fw" label='备注' >
            <el-input type="textarea" v-model="registerForm.demo" placeholder="备注信息"></el-input>
          </el-form-item>
            <el-row>
              <el-col :span="24">
                <el-button  id='submit' @click="add('registerForm')">注册</el-button>
              </el-col>
            </el-row>
        </el-form>  
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';
import path from 'path';
import $ from 'jquery';
import logo from '@/assets/bg_1.jpg';
import cg from '../store/configure.js';
var bp = cg.bp;
let url = bp+'/Login/Index';
//获取窗口大小
let height = $(window).height();

export default {  //必须暴露，否则警告。
	name : 'Login',
	data(){
    var loginNameConfirm = (rule, value, confirm) => {
        this.uniqueStu({loginName:this.registerForm.loginName}).then((data)=>{
          var data3 = data.data.replace(/[\r\n]/g, "");
          if(data3 == 'error'){
            return confirm(new Error('用户名已被注册过，请重新输入'));
          }else if(data3 == 'ok'){
            this.$refs.registerForm.validateField('loginName');
            return confirm();
          }
        }).catch((error)=>{
          console.log(error);
          console.log('服务器错误');
        });
        this.rule = false;
    };
  	return {
        style:"height:"+height+"px",
        fw:'65px',
        registerForm:{
          loginName:'',
          password:'',
          realName:'',
          demo:'',
        },
        rules:{
          // loginName:[
          //   {validator: loginNameConfirm,trigger:'blur'},
          // ],
          // password:[
          //   {validator: validatepassword,trigger:'blur'},
          // ],
          // realName:[
          //   {validator: validaterealName,trigger:'blur'},
          // ],
          loginName:[
            {required:true,message:'请输入用户名',trigger: 'blur'},
          ],
          password:[
            {required:true,message:'请输入密码',trigger: 'blur'},
          ],
          realName:[
            {required:true,message:'请输入真是姓名',trigger: 'blur'},
          ],
        },
  		}
	   },
	created(){
   
  },
  computed:{
    ...mapGetters(['info']),
  },
	methods:{
		add(registerForm){
      this.$refs[registerForm].validate((valid) => {
        if (valid) {
          this.uniqueStu({loginName:this.registerForm.loginName}).then((data)=>{
            var data3 = data.data.replace(/[\r\n]/g, "");
            if(data3 == 'error'){
              this.registerForm.loginName = '';
              return confirm(new Error('用户名已被注册过，请重新输入'));
            }else if(data3 == 'ok'){
              var obj = {
                loginName:this.registerForm.loginName,
                password:this.registerForm.password,
                realName:this.registerForm.realName,
                demo:this.registerForm.demo,
              };
              this.registerUser(obj).then((result)=>{
                if(result.status == 200){
                    this.$notify({
                      title: '成功',
                      message: '注册成功',
                      type: 'success'
                  });
                  this.$router.push('/login');
                }else if(result.status != 350){
                    this.$notify({
                      title: '失败',
                      message: '注册失败',
                      type: 'error'
                    });  
                  }
                });
                }
              })
        }else{
            return false;
        }
      });
    },
     ...mapActions(['registerUser','uniqueStu']),
	},
}
</script>

<style type="text/css" scoped>
/*v2.0*/

*{
    color:#012444;
}
.login {
  position: relative;
  background-image: url('../assets/bg_1.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.login .login_content {
  position: absolute;
  top: 65px;
  bottom: 0;
  left: 0;
  right: 0;
}
.login .login_content .login_title {
  font-size: 28px;
  color: #f2f2f2;
  margin-left: 60%;
  font-weight: bolder;
}
.login .login_content .login_body{
  width: 40%;
  height: 60%;
  margin: 0px auto;
  margin-top: 40px;
  margin-left: 48.5%;
  border-radius: 10px;
  background: #fff;
  animation-name: boxShadowBody;
  animation-duration: 1.5s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}
 .login .login_content .login_body .one {
   margin-left: 25px;
} 
.login .login_content .login_body .one:after {
  content: '';
  display: block;
  clear: both; 
}

.login .login_content .login_body .one .left {
  width: 70%;
  float: left;
}
.login .login_content .login_body .one .right {
  margin-left: 70%;
  width: 28%;
}
.login .login_content .login_body .one .right i {
  height: 100px;
  line-height: 100px;
  color: #0a478a;
  opacity: 0.7;
}
.login .login_content .login_body .one .left>p:nth-child(1) {
  font-size: 22px;
}
.login .login_content .login_body .one .left>p:nth-child(2) {
  font-size: 14px;
  color:  rgb(136, 136, 136);
}

.login .login_content .login_body .two {
  background-color: rgb(238, 238, 238);
   padding: 10px; 
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.login .login_content .login_body .two .el-row:not(:nth-child(3)) {
  margin-bottom: 20px;
}
.login #submit {
  width: 100%;
  /* margin-bottom:1px; */
  background:#0a478a;
  color: #fff;
}


</style>


