<template>
<div class="login" :style="style">
   <!-- v2.0 -->
  <div class="login_content">
    <div class="login_title">
        杰普大数据监测平台
    </div>
    <div class="login_body">
      <div class="one">
        <div class="left">
          <p>欢迎登录</p>
          <p>请输入您的用户名和密码</p>
        </div>
        <div class="right">
          <i class="fa fa-key" style="font-size: 66px;"></i>
        </div>
      </div>
      <div class="two">
        <el-form :model="loginForm" :rules='rules' class='demo-ruleForm form' ref='loginForm'>
        <el-form-item prop='loginName' :label-width="fw" label="用户名">
          <el-input v-model="loginForm.loginName" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item prop='password' :label-width="fw" label="密码">
          <el-input type='password' v-model="loginForm.password" placeholder="请输入密码" @keyup.enter.native="login('loginForm')"></el-input>
        </el-form-item>
          <el-row>
            <el-col :span="24">
              <el-button  id='submit' @click="login('loginForm')">登录</el-button>
              <el-row type="flex" justify="end">
                <el-col :span="10">
                  没有账号，请点击 <el-button type="text" size="small" id="register" @click="register">&nbsp;注册</el-button>
                </el-col>
              </el-row> 
            </el-col>
          </el-row>
        </el-form> 
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';
import path from 'path';
import $ from 'jquery';
import logo from '@/assets/bg_1.jpg'
import cg from '../store/configure.js';
var bp = cg.bp;
//获取窗口大小
let height = $(window).height();

export default {  //必须暴露，否则警告。
	name : 'Login',
	data(){
		return {
      style:"height:"+height+"px",
      fw:'80px',
			loginForm:{},
      rules:{
        loginName:[
          {required:true,message:'请输入用户名',trigger: 'blur'},
        ],
        password:[
          {required:true,message:'请输入密码',trigger: 'blur'},
        ],
      },
		}
	},
	created(){
    
	},
	methods:{
		login(formName){
    //直接登录：
      // sessionStorage.setItem('token','123');
      // this.$router.push('/');
    //权限登录：(真实登录要放开)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var objLogin={
             loginName:this.loginForm.loginName,
             password:this.loginForm.password
          };
          var vm = this;
  			  this.loginIn(objLogin).then((result)=>{
            console.log(result.data.trim());
            if(result.data.trim() == '登录成功'){
              sessionStorage.removeItem('token');//清空token
              var user = this.loginForm.loginName;
              //console.log(this.loginForm.loginName);
              window.sessionStorage.setItem('token',this.loginForm.loginName);
              //跳转到主页home：
              vm.$router.push('/');
            }else{
              this.$message(result.data);
            }
		      }).catch((error)=>{
            console.log('login功能后台报错'+error);
          });
		    } else {
          return false;
        }
      }); 
    },
    register(){
        this.$router.push('/register');
    },
    ...mapActions(['loginIn']),
	},
}
</script>

<style type="text/css" scoped>
/*v2.0*/
.login {
  position: relative;
  background-image: url('../assets/bg_1.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.login .login_content {
  position: absolute;
  top: 80px;
  bottom: 0;
  left: 0;
  right: 0;
}
.login .login_content .login_title {
  margin-left: 61%;
  font-size: 28px;
  color: #f2f2f2;
  font-weight: bolder;
}
.login .login_content .login_body{
  width: 40%;
  margin: 0px auto;
  margin-top: 4%;
  margin-left: 50%;
  border-radius: 10px;
  background: #fff;
  animation-name: boxShadowBody;
  animation-duration: 1.5s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}
.login .login_content .login_body .one {
  padding:8px;
  margin-left: 25px;
}
.login .login_content .login_body .one:after {
  content: '';
  display: block;
  clear: both;
}

.login .login_content .login_body .one .left {
  width: 70%;
  float: left;
}
.login .login_content .login_body .one .right {
  margin-left: 70%;
  width: 28%;
}
.login .login_content .login_body .one .right i {
  height: 100px;
  line-height: 100px;
  color: #0a478a;
  opacity: 0.7;
}
.login .login_content .login_body .one .left>p:nth-child(1) {
  font-size: 22px;
}
.login .login_content .login_body .one .left>p:nth-child(2) {
  font-size: 14px;
  color:  rgb(136, 136, 136);
}

.login .login_content .login_body .two {
  background-color: rgb(238, 238, 238);
  padding: 12px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.login .login_content .login_body .two .el-row:not(:nth-child(3)) {
  margin-bottom: 10px;
}
.login #submit {
  width: 100%;
  margin-bottom:8px;
  background:#0a478a;
  color: #fff;
}
#register{
  text-decoration: none;
  color:#012444;
  font-size: 20px;
}

</style>


