import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home.vue'
import Login from '@/pages/login.vue'
import Register from '@/pages/register.vue'

import IndexW from '@/pages/goods/index.vue'


Vue.use(Router)

let router = new Router({
  routes: [
  	{
  		path:'/login',//登录
      component:Login,
    },{
      path:'/register',//登录
      component:Register,
    },{
      path: '/',//主页
      name: 'Home',
      component: Home,
      redirect:'indexW',
      children:[{
        path:'indexW',
        name:'indexW',
        component:IndexW
      }]
    },
  ]
})

export default router;






