<!-- 商品列表模块-->
<template>
  <div class="home">
    <div class="jp-app-container">
      <div class="jp-home-header">
        <div class="logo">
           <div id='logoDiv' style='display:inline-block;'>
          </div> 
          <div id='homeDiv'>{{msg}}</div>
        </div>
        <div class="right">
          <ul class="userInfo">
            <li><i class="userInfoIcon fa fa-user-circle-o"></i></li>
            <li>{{this.token}}</li> 
          </ul>
          <div class="logout" @click='quit'>
            退出
          </div>
        </div>  
      </div>
      <div class="jp-home-body">
        <div class="jp-home-body-left" style="background-color:#FAFCFB;">
          <el-table :data="tableData" :height="tblHeight"  @cell-click="clickToDetail" width='100%' class="table">
            <el-table-column align="center" prop="key" label="商品列表"></el-table-column>
          </el-table>
        </div>
        <div class="jp-home-body-right">
          <div class="v1" v-if="flags>1">
            <h3 style="height:20px;margin-left:10px;">商品编号：{{goodId}}</h3>
            <div id="info" :style="{height:H2}"></div>
            <div id="info1" :style="{height:H2}"></div>
          </div>
          <div class="v2" v-if="flags==1">
            <div class="welcome-word">欢迎来到大数据监测平台...</div>
          </div>
        </div>
      </div>
  </div>
</div>
</template>

<script>
  import {mapActions,mapGetters} from 'vuex';
  import $ from 'jquery';
  import path from 'path';
  import qs from 'querystring';
  import cg from '../store/configure.js';

  let echarts = require('echarts');
  require('echarts/lib/component/tooltip');
  var symbol = 'circle';
  var symbolSize = 2;//线中点的大小
  var arr = [];
  var legendSelected = {
    '商品权重数据(学生)':true,
    '商品实时数据(学生)':true,
    '商品权重数据(教师)':true,
    '商品实时数据(教师)':true,
  };
  export default {
    name: 'home',
    data () {
      return {
        msg: '商品实时数据监测系统',
        token:'',
        tblHeight:$(window).height()-130,
        tableData:[],
        border:true,
        a:'点击查看商品详情',
        total:'',
        flag:1,
        goodId:'',
        height:'',
        arry1Stu:[],  //权重实时数据学生
        arry2Stu:[],  //商品实时数据学生
        arry1Tch:[],  //权重实时数据老师
        arry2Tch:[],  //权重实时数据老师
        arrxCB:[],
        arrxCB1:[],
        // 控制是欢迎页面展示还是socket展示
        flags:1
      }
    },
    created(){
      this.token = window.sessionStorage.getItem('token');
      this.initWebpack();
      this.initWebpack1();
      this.H2 = ($(window).height() - 130)/2-20 + 'px';
      // 再次刷新页面时展示的商品编号和页面
      this.goodId = window.sessionStorage.getItem('key')
      if(window.sessionStorage.getItem('flags')){
        this.flags = window.sessionStorage.getItem('flags');
      }else{
        this.flags = 1;
      }
    },
    watch:{
      // 教师数据
      arry1Tch:function (val, oldVal) {
        this.linePicture();
      },
      arry2Tch:function (val, oldVal) {
        this.linePicture();
      },
      // 学生数据
      arry1Stu:function (val, oldVal) {
        this.linePicture1();
      },
      arry2Stu:function (val, oldVal) {
        this.linePicture1();
      },
    },
    mounted(){ 
      this.linePicture()
      this.linePicture1()
    },
    methods:{
      ...mapActions(['loginOut','information']),
      // 退出登录
      quit(){
        let vm = this;
        this.$confirm('您确定要退出登录吗？','提示',{
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.loginOut().then((data)=>{
              var data2 = data.data.replace(/[\r\n]/g, "");
             if(data2 == '退出登录'){
                vm.websock.close()
                vm.websock1.close()
                window.sessionStorage.removeItem('token');
                window.sessionStorage.removeItem('key');
                window.sessionStorage.removeItem('flags');
                this.$router.push('/login');   
              }else{
                // console.log('退出失败！后台返回状态码：'+result.status);
                this.$notify({
                  title: '失败',
                  type: 'info',
                  message: result.status
                });
              }
            }).catch((error)=>{
              // console.log('登出操作失败，后台报错'+error);
              this.$notify({
                title: '失败',
                type: 'info',
                message: error
              });
            });
          }).catch(() => {
            this.$notify({
              type: 'info',
              message: '已取消登出'
            });          
          });
      },
      // 点击左侧栏的数据右侧数据进行变化
      clickToDetail(row,column){
        this.arry1Stu = [];
        this.arry2Stu = [];
        this.arry1Tch = [];
        this.arry2Tch = [];
        this.arrxCB = [];
        this.arrxCB1 = [];
        this.goodId = row.key;
        this.websock.send(row.key);
        window.sessionStorage.setItem('key',row.key);
        this.flags++;
        window.sessionStorage.setItem('flags',this.flags);
      },
      // 初始化websocket数据
      initWebpack(){
        const wsuri = "ws://"+cg.bs+"/MyWeb/wbSocketTeacher?topic=";
        this.websock = new WebSocket(wsuri);              
        this.websock.onopen = this.websocketopen;
        this.websock.onmessage = this.websocketonmessage;
        this.websock.onclose = this.websocketclose;
        this.websock.onerror = this.websocketerror;
      },
      // 打开websocket
      websocketopen(){
        this.websock.send("teacher");
        if(this.flag <= 2){
          this.information("teacher");
        }
      },
      // 开始发送数据
      websocketonmessage(e){ 
        var data = JSON.parse(e.data);
        // console.log(data,'老师数据--------------------------');
        if(data.key == window.sessionStorage.getItem('key')){
          // 时间数组
          if(this.arrxCB.length <= 10){ 
            this.arrxCB.push(data.window);
          }else if(this.arrxCB.length>10){
            this.arrxCB.shift(this.arrxCB[0].window);
            this.arrxCB.push(data.window);
          }
          // 教师 平均数数组
          if(this.arry1Tch.length <= 10){ 
            this.arry1Tch.push(data.avg);
          }else if(this.arry1Tch.length>10){
            this.arry1Tch.shift(this.arry1Tch[0].avg);
            this.arry1Tch.push(data.avg);
          }
          // 教师实时数组
          if(this.arry2Tch.length <= 10){ 
            this.arry2Tch.push(data.now);
          }else if(this.arry2Tch.length>10){
            this.arry2Tch.shift(this.arry2Tch[0].now);
            this.arry2Tch.push(data.now);
          }
        }
      },
      linePicture(){
        var vm = this;
        line();
        // 绘制图表
        function line(){
          var option = {
            color: ['red','pink'],
            title:{
              text:'商品实时数据展示(教师)',
              left:'center',
              top:'20',
              subtext:vm.key,
              subtextStyle:{ 
                color:'#aaa',
                fontWeight:'bolder',
                align:'left'
              },
            },
            tooltip: { //鼠标悬浮是出现的效果
              show:true,
              trigger:'axis',
              axisPointer:'shadow',
              snap:'snap',
              formatter: function(params){
                var str = params[0].seriesId + '<br/>' +params[0].axisValue + '<br/>'+params[0].data;
                return str;
              },
              textStyle:{//文本样式。
                color:['white'],
              },
            },
            legend: {
              orient:'vertical',
              data:['商品权重数据(老师)','商品实时数据(老师)'],
              selected:legendSelected,
              x:'right',
              top:80,
              padding:40,
              fontSize:'30px',
            },
            calculable : true,
            grid: {  //图表的内外边距
              top:140,
              bottom:50,
              left:50,
            },
            xAxis: [{  //X轴坐标参数
              name:'时间/秒',
              type: 'category',
              boundaryGap : false, //分界线范围
              data: vm.arrxCB,
              show: true,
            }],
            yAxis: [{ 
              name:'涨幅/秒',
              type: 'value',
              scale:true,
            }],
            series: [{    
              name:'商品权重数据(老师)',
              smooth:true, //曲线平滑属性。
              type:'line',
              symbol:symbol,
              symbolSize:symbolSize,
              data: vm.arry1Tch,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            },{    
              name:'商品实时数据(老师)',
              smooth:true, //曲线平滑属性。
              type:'line',
              symbol:symbol,
              symbolSize:symbolSize,
              data: vm.arry2Tch,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            }],
          }
          echarts.init(document.getElementById('info')).setOption(option);
        }
        return line;
      },
      initWebpack1(){
        const wsuri1 = "ws://"+cg.bs+"/MyWeb/wbSocket?topic=";
        this.websock1 = new WebSocket(wsuri1);              
        this.websock1.onopen = this.websocketopen1;
        this.websock1.onmessage = this.websocketonmessage1;
        this.websock1.onclose = this.websocketclose1;
        this.websock1.onerror = this.websocketerror1;
      },
      websocketopen1(){
        // console.log("学生WebSocket连接成功")
        this.websock1.send(window.sessionStorage.getItem('token'));
        if(this.flag <= 2){
          this.information(window.sessionStorage.getItem('token'));
        }
      },
      websocketonmessage1(e){
        var data = JSON.parse(e.data);
        // console.log(data,"学生数据--------------------------");
        // 左侧数据
        if(this.tableData.length < 200){
          if(this.tableData.length !== 0){
            let a = this.tableData.some((item,index)=>{
              return item.key == data.key
            })
            if(!a){
              this.tableData.push(data);
            }
          }else{
            this.tableData.push(data);
          }
        }
        // 学生平均数数组
        if(data.key == window.sessionStorage.getItem('key') ){
          // 时间数组
          if(this.arrxCB1.length <= 10){ 
            this.arrxCB1.push(data.window);
          }else if(this.arrxCB.length>10){
            this.arrxCB1.shift(this.arrxCB1[0].window);
            this.arrxCB1.push(data.window);
          }
          if(this.arry1Stu.length <= 10){ 
            this.arry1Stu.push(data.avg);
          }else if(this.arry1Stu.length>10){
            this.arry1Stu.shift(this.arry1Stu[0].avg);
            this.arry1Stu.push(data.avg);
          }
          // 学生实时数组
          if(this.arry2Stu.length <= 10){ 
            this.arry2Stu.push(data.now);
          }else if(this.arry2Stu.length>10){
            this.arry2Stu.shift(this.arry2Stu[0].now);
            this.arry2Stu.push(data.now);
          }
        }
      },
      websocketclose(){  
        // console.log("WebSocket关闭教师");
      },
      websocketclose1(){  
        // console.log("WebSocket关闭学生");
      },
      linePicture1(){
        var vm = this;
        line1();
        // 绘制图表
        function line1(){
          var option = {
            color: ['#0c498c','#7c9ad6'],
            title:{
              text:'商品实时数据展示(学生)',
              left:'center',
              top:'20',
            //  subtext:'编号',  副标题
              subtext:vm.key,
              subtextStyle:{ 
                color:'#aaa',
                fontWeight:'bolder',
                align:'left'
              },
            },
            tooltip: { //鼠标悬浮是出现的效果
              show:true,
              trigger:'axis',
              axisPointer:'shadow',
              snap:'snap',
              formatter: function(params){
                var str = params[0].seriesId + '<br/>' +params[0].axisValue + '<br/>'+params[0].data;
                return str;
              },
              textStyle:{//文本样式。
                color:['white'],
              },
            },
            legend: {
              orient:'vertical',
              data:['商品权重数据(学生)','商品实时数据(学生)'],
              selected:legendSelected,
              x:'right',
              top:80,
              padding:40,
              fontSize:'30px',
            },
            calculable : true,
            grid: {  //图表的内外边距
              top:140,
              bottom:50,
              left:50,
            },
            xAxis: [{  //X轴坐标参数
              name:'时间/秒',
              type: 'category',
              boundaryGap : false, //分界线范围
              data: vm.arrxCB1,
              show: true,
            }],
            yAxis: [{ 
              name:'涨幅/秒',
              type: 'value',
              scale:true,
            }],
            series: [{
              name:'商品权重数据(学生)',
              smooth:true, //曲线平滑属性。
              type:'line', //曲线类型（线状）
              symbol:symbol,
              symbolSize:symbolSize,//曲线点的大小
              data: vm.arry1Stu,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            },{    
              name:'商品实时数据(学生)',
              smooth:true, //曲线平滑属性。
              type:'line',
              symbol:symbol,
              symbolSize:symbolSize,
              data: vm.arry2Stu,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            } ],
          }
          echarts.init(document.getElementById('info1')).setOption(option);
        }
        return line1;
      },
    },
    destroyed(){
      this.websock.close()
      this.websock1.close()
      window.sessionStorage.removeItem('key');
      window.sessionStorage.removeItem('flags');
    },
  }
</script>

